<template>
<div class="movie">
  <MovieCard :movie_id="movie_id" :token="token"/>
  <ReviewCard :movie_id="movie_id" :token="token"/>

</div>
</template>

<script>

import MovieCard from '../components/shared/Movie-card'
import ReviewCard from '../components/shared/Review-card'
import WriteReview from '../components/shared/buttons/Write-review'

export default {
  name: 'Movie',
  components:
    { MovieCard, ReviewCard, WriteReview }
  ,
  data(){
    return {
      movie_id: this.$route.params.movie_id,
      movie: '',
      credits: '',
      url_poster: process.env.VUE_APP_API_POSTER,
    }
  },
  computed:{
     token : function(){ return this.$store.getters.isAuth}
  }
}
</script>

